import React from 'react';
import logo from './logo.svg';
import './App.css';
import { BrowserRouter as Router, Route, Link, Switch, NavLink } from 'react-router-dom';

const baseUrl = process.env.PUBLIC_URL + '/';

function Index() {
  return (
    <div className="jumbotron">
      <h1 className="display-4">Daniel Sousa</h1>
      <p className="lead">Welcome to my homepage - Enjoy the journey</p>
      <hr className="my-4" />
      <p>Where you will find a set of information about my personal and professional interest</p>
      <Link className="btn btn-primary btn-lg" to={ baseUrl + 'moto' } role="button">Let's Start</Link>
      <img src="moto_1.webp" type="image/webp" alt="" />
    </div>
  );
}

function Moto() {
  return (
    <div className="container mt-3">
      <div className="row">
        <div className="col">
          <div className="card">
            <h5 className="card-header">Motorcycle</h5>
            <div className="card-body">
              <h5 className="card-title">Adventures and web info for your motorcycle</h5>
              <p className="card-text">Under this section you will find photos, videos and links to motorcycle gear</p>
            </div>
            <div className="card-body">
              <Link className="card-link" to={ baseUrl + 'moto' }>Photos</Link>
              <Link className="card-link" to={ baseUrl + 'moto' }>Videos</Link>
              <Link className="card-link" to={ baseUrl + 'moto' }>Links</Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

function Prog() {
  return <h2>Computer programming</h2>;
}

function NoMatch({ location }) {
  return (
    <div className="alert alert-danger" role="alert">
      <h4 className="alert-heading">Error Oops!</h4>
      <p>No match for <code>{ location.pathname }.</code></p>
      <hr />
      <a className="btn btn-info" href={ baseUrl }>Go back to home!</a>
    </div>
  );
}

function App() {
  return (
    <div className="App">
    <Router>
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <NavLink className="navbar-brand h1" to={ baseUrl }>Daniel Sousa</NavLink>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav">
            <li className="nav-item">
              <Link className="nav-link" to={ baseUrl + 'moto' }>Motocycle</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to={ baseUrl + 'prog' }>Computer programming</Link>
            </li>
          </ul>
        </div>
      </nav>
        <Switch>
          <Route exact path={ baseUrl } component={ Index } />
          <Route path={ baseUrl + 'moto' } component={ Moto } />
          <Route path={ baseUrl + 'prog' } component={ Prog } />
          <Route component={ NoMatch } />
        </Switch>
      </Router>
      <footer className="page-footer font-small blue">
        <div className="footer-copyright text-center py-3">
          Copyright 2019 - Daniel Sousa &lt;da.arada'at'gmail.com&gt;
        </div>
      </footer>
    </div>
  );
}

export default App;
